@media (prefers-reduced-motion: no-preference) {
    .elementOne {
      animation: elementOneSpin infinite 10s linear;
    }
  }
  
  @keyframes elementOneSpin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  

