@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&family=Lato&family=Merriweather:wght@300;400;700&display=swap');

.App {
  text-align: center;
}

a:link {
  color: darkblue;
  font-size: 40px;
  font-weight: 700;
  font-family: 'Lato', sans-serif;
  cursor: pointer;
  text-decoration: none;
}


a:hover {
  color: rgb(231, 74, 74);
}


h1 {
  color: #2463EB;
  font-size: 40px;
  font-weight: 700;
  font-family: 'Lato', sans-serif;
}

p {
  color: #2463EB;
  font-size: 30px;
  font-weight: 700;
  font-family: 'Lato', sans-serif;
}

.nothingYet{
  font-family: 'Inter', sans-serif;
  font-family: 'Lato', sans-serif;
  font-family: 'Merriweather', serif;
}